
.teampage-heading{
    text-transform: uppercase;
    font-family: Rubik;
    font-size: 3rem;
    letter-spacing: 3px;
    margin-bottom: 5rem;
    text-align: center;
    position: relative;
    animation: fadein 3s ease-in;
    animation-fill-mode: forwards;
    padding-top: 10%;
  }
  
  @keyframes fadein{
    from{
      color: transparent;
    }
    to{
      color: #333;
    }
  }
  
  .teampage-heading::after{
    content: "";
    height: 1rem;
    background-color: #026670;
    position: absolute;
    bottom: -2rem;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 2rem;
    animation: underline-grow 2s ease-out;
    animation-delay: 1s;
    animation-fill-mode: forwards;
  }
  
  @keyframes underline-grow{
    from{
      width: 0;
    }
    to{
      width: 30%;
      height: 5%;
    }
  }
  @media(max-width: 770px){
    .teampage-heading{
      padding-top: 25%;
      font-size: 1.4rem;
      letter-spacing: 1px;
    }
  
    @keyframes underline-grow{
      from{
        width: 0;
      }
      to{
        width: 30%;
      height: 5%;
      }
    }
}

.wrapper{
    margin-top: 50px;
    padding-top: 5%;
    font-family: Karla;
  }
  
  .wrapper h1{
    font-family: 'Karla';
    font-size: 52px;
    margin-bottom: 60px;
    text-align: center;
  }
  
  .team{
    display: flex;
    justify-content: center;
    width: auto;
    text-align: center;
    flex-wrap: wrap;
  }
  
  .team .team_member{
    background: #fff;
    margin: 5px;
    margin-bottom: 50px;
    width: 300px;
    padding: 20px;
    line-height: 20px;
    color: #8e8b8b;  
    position: relative;
  }
  
  .team .team_member h3{
    color: #026670;
    font-size: 26px;
    margin-top: 50px;
  }
  
  .team .team_member p.role{
    color: #ccc;
    margin: 12px 0;
    font-size: 12px;
    text-transform: uppercase;
  }
  
  .team .team_member .team_img{
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: #fff;
  }
  
  .team .team_member .team_img img{
    width: 100px;
    height: 100px;
    padding: 5px;
  }
  

